.popup {
	position: absolute;
	width: 100%;
	height: calc(100% - 149px);
	left: 0px;
	top: 149px;
	background: #FFFFFF;
	box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
	border-radius: 40px 40px 0px 0px;
	padding: 40px 0 0 0;
	text-align: center;
	font-size: 0;
	transition: transform 0.3s linear 0s;
    transform: translateY(0%);
    opacity: 1;
    
    &__inp {
        width: 100%;
        border: 2px solid #E6E6E6;
        border-radius: 10px;
        margin-bottom: 30px;
        font-weight: 500;
        font-size: 16px;
        color: #333333;
        padding: 20px;
    }

    &__inp {
       &_add-task {
            height: 160px;
            resize: none;
        }

        &_edit-task {
            height: 50px;
        }
    }

    &__row {
        display: flex;
        justify-content: center;
    }

    &__btn-close {
        background: #F2F2F2;
        margin-right: 21px;
        @include btn();
    }

    &__btn-add, &__btn-save {
        background: #23A3FF;
        color: #FFFFFF;
        @include btn();
    }
}

.btn-add {
    position: fixed;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.1);
    color: #23A3FF;
    font-size: 53px;
    text-align: center;
    right: 40px;
    bottom: 40px;
    cursor: pointer;
}


@media screen and (min-width: 1400px) {
	.popup {
		width: 800px;
		height: 350px;
		box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
		border-radius: 40px;
		transform: translate(-50%, 0%);
		transition: transform 0.5s ease 0s;
		left: 50%;
	}
}